import React from 'react'
import {Link} from 'react-router-dom';
export default function Navbar(props) {
    return (
        <div>
        <nav className="navbar navbar-expand-lg navbar-dark bg-transparent">
  <div className="container-fluid">
    <Link className="navbar-brand" to='home'>Noxe</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
    {props.tokenData != null?
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link" aria-current="page" to='home'>Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='tv'>Tv show</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='movies'>Movies</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='people'>People</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='about'>About</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='network'>Network</Link>
        </li>
      </ul>:''}
      {props.tokenData ==null?
        <ul className="navbar-nav mb-2 mb-lg-0 ms-auto">
          <li className='order-lg-last order-first'>
          <Link className="nav-link" to='/login'>Login</Link>
          </li>
          <li className='order-lg-last order-first'>
            <Link className="nav-link" to='/rejester'>Rejester</Link>
          </li>
        </ul>:
        <ul className="navbar-nav mb-2 mb-lg-0">
          <li className='order-lg-first order-last'>
          <div className='mt-2'>
            <i className='fab mx-2 fa-facebook'></i>
            <i className='fab mx-2 fa-twitter'></i>
            <i className='fab mx-2 fa-instagram'></i>
            <i className='fab mx-2 fa-spotify'></i>
            <i className='fab mx-2 fa-soundcloud'></i>
          </div>
        </li>
        
        <li className='order-lg-last order-first'>
          <Link className="nav-link" to='logout' onClick={props.logOut}>Logout</Link>
        </li>
        </ul>
    }
    </div>
  </div>
</nav>
    </div>
    )
}
